.light-theme {
    --body-color: #fafafa;
    --base-color: #ffffff;
    --hover-color: #F4F4F5;
}

.main-panel {
    position: absolute;
    width: calc(100% - 250px);
    left: 250px;
    min-height: 100vh;
    transition: 0.3s all ease-in-out !important;
    overflow-x: hidden;
    .page-heading {
        font-weight: 600;
    }
}

@media (max-width: 992px) {
    .main-panel {
        width: 100%;
        left: 0;
    }
    .main-panel {
        &.active {
            width: calc(100% - 250px);
            left: 250px;
            .header-actions {
                visibility: hidden !important;
            }
        }
    }
}

@media (max-width: 480px) {
    .toggle.active {
        position: fixed;
        z-index: 10000;
        right: 25px;
        left: initial;
        transition: 0.5s ease-in-out;;
    }
    .main-panel {
        width: 100%;
        left: 0;
        transition: 0.3s all ease-in-out !important;
        &.active {
            .header-actions {
                visibility: hidden !important;
            }
        }
    }
}
