header {
    background-color: var(--base-color);
    // padding: 1rem;
    height: 60px;

    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    border-bottom: 1px solid var(--border-dim);
    .toggle {
        position: relative;
        width: 30px;
        cursor: pointer;
        svg {
            width: 30px;
            path {
                fill: var(--text-max);
            }
        }
        // img {
        //     width: 100%;
        //     height: 100%;
        //     filter: none;
        //     filter: invert(75%) sepia(9%) saturate(596%) hue-rotate(201deg) brightness(89%) contrast(84%);
        //     &:hover {
        //         // filter: invert(0%) sepia(100%) saturate(7460%) hue-rotate(97deg) brightness(116%) contrast(92%);
        //     }
        // }
    }
    .profile-dd {
        .dropdown-toggle {
            background-color: var(--hover-color);
            padding: 5px 10px;
            border-radius: 5px;
        }
    }
}


