.nav-wrapper {
    display: flex;
    width: 250px;
    height: 100vh;
    overflow-x: hidden;
    position: fixed;
    z-index: 10;
    flex-shrink: 0;
    background: var(--base-color);
    border-right: 1px solid var(--border-dim);
    // box-shadow: 6px 0px 18px #f1f1f1;
    // box-shadow: 0px 0px 10px #f1f1f1;
    .side-bar {
        width: 100%;
        overflow-y: auto;
        .side-nav-header {
            display: flex;
            width: 100%;
            align-items: center;
            a {
                text-decoration: none;
                color: var(--primary-color);
                img {
                    width: 100px;
                }
            }
        }
        .sidebar-menu {
            .menu-item {
                margin: 10px auto;
                .menu-item-icon {
                    width: 20px;
                    flex-shrink: 0;
                    svg {
                        width: 100%;
                        height: 100%;
                    }
                    img {
                        width: 100%;
                    }
                }
                a {
                    text-decoration: none;
                    color: var(--text-color);
                    transition: 0.3s;
                    font-size: 1rem;
                    display: flex;
                    align-items: center;
                    &:hover {
                        color: var(--primary-color);
                        svg {
                            path {
                                transition: 0.3s;
                                fill: var(--base-color);
                                stroke: var(--primary-color);
                            }
                        }
                    }
                }
                svg {
                    path {
                        fill: var(--base-color);
                        stroke: var(--text-color);
                    }
                }
                a.active {
                    color: var(--primary-color);
                    svg {
                        path {
                            fill: var(--base-color);
                            stroke: var(--primary-color);
                        }
                    }
                }
            }
        }
    }
}


// .nav-wrapper.active {
//     width: 60px;
// }

@media (max-width: 992px) {
    .nav-wrapper {
        left: -250px;
    }
    .nav-wrapper.active {
        left: 0;
        width: 250px;
    }
}

@media (max-width: 480px) {
    .nav-wrapper {
        width: 100%;
        left: -100%;
        z-index: 1000;
    }
    .nav-wrapper.active {
        // width: 100%;
        left: 0;
        width: 275px;
    }
}